import React, { Component } from 'react';
import { DiscussionEmbed } from 'disqus-react';
import { Collapse, Button } from 'react-bootstrap';
import styled from 'styled-components';

const StyledBox = styled.div`
  border: solid 4px;
  width: 75%;
  padding: 10px;
  background: white;
  
`

class CollapseComment extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = { open: false, };
    this.handleClick = this.handleClick.bind(this);
    // this.handleOutsideClick = this.handleOutsideClick.bind(this);
  }
  handleClick() {
    this.setState(state => ({ open: !state.open })
    );
    // if (!this.state.open) {
    //   document.addEventListener('click', this.handleOutsideClick, false);
    // } else {
    //   document.removeEventListener('click', this.handleOutsideClick, false);
    // }
  }
  // handleOutsideClick(e) {
  //   if (this.node.contains(e.target)) {
  //     return;
  //   }
  //   this.handleClick();
  // }

  render() {
    const { open } = this.state;

    const disqusShortname = "hot-dog-101";
    const disqusConfig = {
      // url: this.props.article.url,
      // identifier: this.props.article.id,
      // title: this.props.article.title,
    };

    return (
      // <div ref={node => { this.node = node; }}>
      <div>
        <Button variant='collapse' className='mx-2' onClick={this.handleClick} aria-expanded={open}>
          Comment
        </Button>
        <Collapse in={this.state.open} className='my-4 w-90'>
          <StyledBox className='mx-auto'>
            <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
          </StyledBox>
        </Collapse>
      </div>
    );
  }
}

export default CollapseComment;