import React, { Component } from 'react';
import { Navbar, Nav, Button } from 'react-bootstrap';
import posed from 'react-pose';

import '../index.css';

import logo from '../Assets/logo.png';

const Hover = posed.div({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.15, duration: 200 },
  press: { scale: 1 },
});



class Navigation extends Component {
  render() {
    return (
      <div>
        <Navbar expand='lg'>
          <Nav.Item className='mx-auto w-25 my-4'>
            <Button href="/dailies/" variant='navbar' size='xxl'>Daily Comics</Button>
          </Nav.Item>
          <Navbar.Brand href="/" className='mx-auto'>
            <Hover className='' >
              <img alt='hot-dog' src={logo}
                width='200'
                height='200'
                className='d-inline-block' /> {''}
            </Hover>
          </Navbar.Brand>
          <Nav.Item className='mx-auto w-25'>
            <Button href="/depth/" variant='navbar' size='xxl'>In-Depth Comics</Button>
          </Nav.Item>
        </Navbar >
        <div className='box display-flex mx-auto my-4' />
      </div>
    )
  }
}

export default Navigation;