import React, { Component } from 'react';
import { Collapse, Button } from 'react-bootstrap';
import { FacebookShareButton, TwitterShareButton, TumblrShareButton, RedditShareButton, PinterestShareButton, EmailShareButton } from 'react-share';
import { SocialIcon } from 'react-social-icons';

import styled from 'styled-components';
import '../index.css';

const ShareIcon = styled(SocialIcon)`
  filter: grayscale(100%);
  : hover{
    cursor: pointer;
    filter: grayscale(0%);
  }
`
const ShareBox = styled.div`
  display: inline-block;
  margin: 2px;
`
const StyledBox = styled.div`
  border: solid 4px;
  width: 55%;
  padding: 10px;
  background: white;
  
`
class CollapseShare extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = { open: false, };
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick() {
    this.setState(state => ({ open: !state.open })
    );
  }
  render() {
    const { open } = this.state;
    const shareUrl = 'http://instagram.com/hotdog101comic';
    const title = 'Hot Dog 101';
    return (
      <div>
        <Button variant='collapse' className='mx-2' onClick={this.handleClick} aria-expanded={open}>
          Share
        </Button>
        <Collapse in={this.state.open} className='my-4 w-90'>
          <StyledBox className='mx-auto'>
            <ShareBox>
              <EmailShareButton url={shareUrl} subject={title} body='This is an interactive webcomic, check it out!'>
                <ShareIcon network='email' bgColor='#6c5cd4' style={{ height: 35, width: 35 }} />
              </EmailShareButton>
            </ShareBox>

            <ShareBox>
              <FacebookShareButton url={shareUrl} quote={title}>
                <ShareIcon network='facebook' style={{ height: 35, width: 35 }} />
              </FacebookShareButton>
            </ShareBox>

            <ShareBox>
              <TwitterShareButton url={shareUrl} title={title}>
                <ShareIcon network='twitter' style={{ height: 35, width: 35 }} />
              </TwitterShareButton>
            </ShareBox>

            <ShareBox>
              <RedditShareButton url={shareUrl} title={title}>
                <ShareIcon network='reddit' style={{ height: 35, width: 35 }} />
              </RedditShareButton>
            </ShareBox>

            <ShareBox>
              <TumblrShareButton url={shareUrl} title={title} caption={title}>
                <ShareIcon network='tumblr' style={{ height: 35, width: 35 }} />
              </TumblrShareButton>
            </ShareBox>

            <ShareBox>
              <PinterestShareButton media={shareUrl} description={title}>
                <ShareIcon network='pinterest' style={{ height: 35, width: 35 }} />
              </PinterestShareButton>
            </ShareBox>
          </StyledBox>
        </Collapse>
      </div>
    );
  }
}

export default CollapseShare;