import React, { Component } from 'react';
import { Navbar, Nav, Button } from 'react-bootstrap';
import { SocialIcon } from 'react-social-icons';

import styled, { keyframes } from 'styled-components';
import '../index.css';


const animIcon = keyframes`
from{
  transform: scale(1,1);}
to{
  transform: scale(1.25 , 1.25);}
`

const StyledIcon = styled(SocialIcon)`
  : hover{
    animation: ${animIcon} 0.5s infinite alternate;
    filter: hue-rotate(95deg) brightness(155%);
  }
`

class Footer extends Component {
  render() {
    return (
      <div>
        <div className='box display-flex mx-auto mb-4' />
        <Navbar expand='lg' className='justify-content my-2'>
          <Nav.Item className='justify-content-center mx-auto'>
            <SocialIcon url="http://facebook.com/" fgColor="#ffffff" className='mx-2' style={{ height: 40, width: 40 }} />
            <StyledIcon url="http://instagram.com/hotdog101comic" target='_blank' fgColor="#ffffff" className='mx-2' style={{ height: 40, width: 40 }} />
            <SocialIcon url="http://twitter.com/" fgColor="#ffffff" className='mx-2' style={{ height: 40, width: 40 }} />
          </Nav.Item>
        </Navbar>
        <Navbar>
          <Nav.Item className='mx-auto mb-4'>
            <Button variant='navbar'
              href="/about/"
              className='justify-content-start d-flex'>
              About
            </Button>
          </Nav.Item>
        </Navbar >
      </div>
    )
  }
}

export default Footer;