import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Navigation from './Components/Navigation';
import ExamplePage from './Pages/ExamplePage';
import Home from './Pages/Home';
import Dailies from './Pages/Dailies';
import About from './Pages/About';
import Footer from './Components/Footer';

import './index.css';

class App extends Component {
  render() {
    return (
      <div className='wrapper'>
        <div className='container'>
          <Navigation />
          <Router>
            <div>
              <Route path="/" exact component={Index} />
              <Route path="/depth/" component={InDepthComics} />
              <Route path="/dailies/" component={DailyComics} />
              <Route path='/about/' component={AboutPage} />
            </div>
          </Router>
          <br />
          <Footer />
        </div>
      </div>
    );
  }
}

export default App;

function Index() {
  return (
    <div>
      <Home />
    </div>
  )
}

function InDepthComics() {
  return (
    <div>
      <h1 className='header'>In-Depth Comics</h1>
      <h2 className='header2'>(A.K.A. Documentation)</h2>
      <ExamplePage />
    </div>
  )
}

function DailyComics() {
  return (
    <div>
      <h1 className='header'>Daily Comics</h1>
      <h2 className='header2'>(A.K.A. Placeholders)</h2>
      <Dailies />
    </div>
  )
}

function AboutPage() {
  return (
    <>
      <h1 className='header'>About</h1>
      {/* <h2 className='header2'> </h2> */}
      <About />
    </>
  )
}
