import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import posed from 'react-pose';

import '../index.css';
import './ThreePanel.css';

const Hover = posed.img({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
  },
  hover: {
    scale: 1.05,
  },
  press: {
    scale: 0.95,
  }
});

class ThreePanel extends Component {
  render() {
    return (
      <div>
        <Container>
          <Row center>
            <Panel id='1' src='https://i.ebayimg.com/images/g/lL8AAOSw-ypbmcBb/s-l300.jpg' />
            <Panel id='2' src='http://dachshundtreasures.com/images/colors/11301005.jpg' />
            <Panel id='3' src='http://dogbuns.com/Rocky.jpg' />
          </Row>
        </Container>
      </div>
    );
  }
};

function DateTitle(props) {
  return (
    <h1>{props.title}</h1>
  )
}

function Panel(props) {
  return (
    <Col className='col'>
      <Hover center id={props.id} src={props.src} />
    </Col>
  )
}

export default ThreePanel;
export { Panel, DateTitle };